@font-face {
  font-family: 'LucidaGrande-Bold';
  font-weight: bold;
  src: 
    local('LucidaGrande-Bold'),
    local('LucidaGrande Bold'),
    url(/static/media/375E5F_1_0.102d3585.eot) format('eot'),
    url(/static/media/375E5F_1_0.685d27af.ttf) format('ttf'),
    url(/static/media/375E5F_1_0.a6f64507.woff) format('woff'),
    url(/static/media/375E5F_1_0.c714814b.woff2) format('woff2');
}

@font-face {
  font-family: 'LabGrotesque-Light';
  font-weight: light;
  src: 
    local('LabGrotesque-Light'),
    local('LabGrotesque Light'),
    url(/static/media/LabGrotesque-Light.1f6d2da8.eot) format('eot'),
    url(/static/media/LabGrotesque-Light.70f4100b.woff) format('woff'),
    url(/static/media/LabGrotesque-Light.7b306326.woff2) format('woff2');
}

@font-face {
  font-family: 'RoobertTRIAL-SemiBold';
  font-weight: normal;
  src: 
    local('RoobertTRIAL-SemiBold'),
    local('RoobertTRIAL SemiBold'),
    url(/static/media/375E5F_0_0.cf8aca26.eot) format('eot'),
    url(/static/media/375E5F_0_0.63030e21.ttf) format('ttf'),
    url(/static/media/375E5F_0_0.f3296507.woff) format('woff'),
    url(/static/media/375E5F_0_0.c799eac3.woff2) format('woff2');
}

html{
  height: 100%;
}

body {
  margin: 0;
  padding: 0 48px;
  font-family: -apple-system, LabGrotesque-Light, "Ubuntu", sans-serif;
  font-weight: normal;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.page-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.mobile-menu {
  cursor: pointer;
}
.page-footer {
  flex-shrink: 0;
}
.page-footer p {
  opacity: 0.8;
  font-family: LabGrotesque-Light;
  font-size: 13px;
  font-weight: normal;
  font-style: normal;
  font-stretch: normal;
  line-height: 1.69;
  letter-spacing: 0.2px;
  text-align: center;
  color: #161e2e;
}
.cv-title {
  font-family: -apple-system, RoobertTRIAL, "Roboto", sans-serif;
  font-size: 28px;
  font-weight: 600;
  font-style: normal;
  font-stretch: normal;
  line-height: 1.14;
  letter-spacing: normal;
  color: #161e2e;
  margin-bottom: 40px;
}
.cv-title span {
  font-family: -apple-system, LucidaGrande-Bold, "Roboto", sans-serif;
  font-weight: 'normal';
}
.cv-form {
  width: 365px;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 0 auto;
}
input, textarea {
  width: 100%;
  box-sizing: border-box;
  border-radius: 20px;
  border: solid 1px #d9dbde;
  padding: 9px 14px 9px 14px;
  opacity: 0.6;
  font-family: LabGrotesque-Light;
  font-size: 17px;
  font-weight: 300;
  font-style: normal;
  font-stretch: normal;
  line-height: 1.41;
  letter-spacing: 0.2px;
  color: #737882;
  margin-bottom: 15px;
  outline: none;
  -webkit-transition: all 1s ease;
  transition: all 1s ease;
}
input.error {
  border: solid 1px red;
}
.validateField {
  width: 100%;
  position: relative;
}

.errorMessage {
  position: absolute;
  top: -21px;
  right: 11px;
  font-size: 10px;
  color: #ec4a4a;
}

textarea {
  height: 84px;
  max-width: 100%;
  margin-bottom: 35px;
}

.lastInput {
  margin-bottom: 36px;
}

.select {
  margin-bottom: 36px;
  width: 100%;
  box-sizing: border-box;
  border-radius: 20px;
  border: solid 1px #d9dbde;
  padding: 9px 14px 9px 14px;
  opacity: 0.6;
  font-family: LabGrotesque-Light;
  font-size: 17px;
  font-weight: 300;
  font-style: normal;
  font-stretch: normal;
  line-height: 1.41;
  letter-spacing: 0.2px;
  color: #737882;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  outline: none;
}

select::-ms-expand { 
    display: none; /* remove default arrow in IE 10 and 11 */
}

input[type="file"].inputfile {
  border: none;
  font-size: 16px;
}
input[type="file"].inputfile::-webkit-file-upload-button {
  visibility: hidden;
}
input[type="file"].inputfile::before {
  content: 'Upload CV file';
  display: inline-block;
  vertical-align: top;
  outline: none;
  white-space: nowrap;
  -webkit-user-select: none;
  cursor: pointer;
  font-size: 16px;
  text-decoration: underline;
}
input[type="file"].inputfile:hover::before {
  border-color: black;
}
input[type="file"].inputfile:active::before {
  background: -webkit-linear-gradient(top, #e3e3e3, #f9f9f9);
}
.success-message {
  position: absolute;
  left: 50%;
  -webkit-transform: translate(-50%, -130px);
          transform: translate(-50%, -130px);
  color: forestgreen;
  font-size: 18px;
  -webkit-transition: all 3s ease;
  transition: all 3s ease;
}
.defaultButton {
  width: 100%;
  padding: 11px 0 11px 0;
  border-radius: 4px;
  border: solid 1px transparent;
  box-shadow: 0 5px 10px 0 rgba(22, 30, 46, 0.2);
  background-color: #ffffff;
  font-family: LabGrotesque-Light;
  font-size: 17px;
  font-weight: 300;
  font-style: normal;
  font-stretch: normal;
  line-height: normal;
  letter-spacing: 0.2px;
  text-align: center;
  color: #000000;
  outline: none;
  cursor: pointer;
}
.App {
  text-align: center;
  display: flex;
  flex-direction: column;
  height: 100vh;
}
.page-main {
  flex-grow: 1;
}
